.ourteamcard{
    padding: 20px;
    display: flex;
    flex-direction: column;
    padding-block: 24px;
    border-radius: 5px;
    background: linear-gradient(145deg, #f1f4fa, #cbcdd3);
    box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001;
    height: 370px;
    width: 300px;
    justify-content: space-between;
    align-items: center;
    margin: auto auto 15px auto;
}

.image-wrp{
    height: 166px;
    overflow: hidden;
    border: none;
    border-radius: 4px;
    outline: none;
    box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001;
    position: relative;
}

.infotxt{
    border: none;
    height: 32px;
    border-radius: 10px;
    background: linear-gradient(145deg, #f1f4fa, #cbcdd3);
    outline: none;
    font-family: "Nunito Sans", sans-serif;
    box-shadow: 4px 4px 6px 0 rgb(255 255 255 / 50%), -4px -4px 6px 0 rgb(116 125 136 / 20%), inset -4px -4px 6px 0 rgb(255 255 255 / 50%), inset 4px 4px 6px 0 rgb(116 125 136 / 30%);
    padding-inline: 20px;
    margin-bottom: 13px;
    color: #6c6b6b;
    padding-top: 4px;
}

.ourteam-icon{
    display: flex;
    column-gap: 20px;
    justify-content: center;
}

.ourteam-icon a{
    height: 40px;
    width: 40px;
    padding: 10px;
    box-shadow: -7px -7px 20px 0px #fff9, -4px -4px 5px 0px #fff9, 7px 7px 20px 0px #0002, 4px 4px 5px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001, inset 0px 0px 0px 0px #fff9, inset 0px 0px 0px 0px #0001;
    border-radius: 50%;
    display: block;
}

