.ourteamwrapper {
  padding: 20px;
  background: #e5e8ef;
  text-align: center;
}

.devcardswrp,
.coordcardswrp {
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 20px;
  row-gap: 10px;
  margin: 30px auto 60px;
}

@media (min-width: 576px) {
  .devcardswrp,
  .coordcardswrp {
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
  }
}

@media (min-width: 992px) {
  .devcardswrp,
  .coordcardswrp {
    grid-template-columns: repeat(3, 1fr);
    width: 80%;
  }
}

